@import "./../Mixin_New.scss";
@import "./../Variable.scss";

.Message-container {
    position: fixed;
    // display: none;
    width: 100%;
    height: 70px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $bg-light;
    color: black;
    font-family: Arial, Helvetica, sans-serif;
    z-index: 9999;
    cursor: pointer;

    // padding: 1rem;

    .Message-back-div {
        padding: 0rem 1rem;
        padding-top: 1rem;
        display: flex;
        align-items: center;

        .Message-User-input {
            border: none;
            padding-inline-start: 1rem;

            &:focus {
                border: none;
                box-shadow: none;
            }
        }
    }

}

.Message-user-List {
    position: relative;
    left: 0;
    right: 0;
    bottom: 0;
    background: $bg-light;
    padding: 1rem;
    padding-bottom: 2rem;
    color: black;
    z-index: 2;
    padding-top: 2rem;


    .fbox {
        width: 70px;
        height: 70px;
        background: blue;
        margin: 0.3rem;
    }

    .Message-user-container {
        padding-top: 1rem;

        .Message-profile-div {
            display: flex;
            align-items: center;

            .Message-profile-img {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;
                object-position: center;
                box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
            }

            .Message-Profile-name {
                margin-left: 1rem;
            }
        }
    }
}

.Message-user-bottom {
    width: 100%;
    height: 70px;
    position: fixed;
    bottom: 0;
    background: $bg-light;
    z-index: 9999;
}

.userimg {
    width: 70px;
    height: 70px;
    object-fit: cover;
    border-radius: 50%;
    padding: 0.5rem;
}

// Tab
.Message-user-List {
    display: flex;
    flex-direction: column;

    .tab-title {
        color: #FF0000;
    }
    .online-user-name{
        margin-left: 0.5rem;
        font-weight: 600;
        text-transform: capitalize;
    }

    .tab-block {
        display: flex;
        justify-content: space-between;
        margin-bottom: 2rem;

        .w3-button {
            background: none;
            border: none;
            font-size: 18px;
            font-weight: 600;
        }
    }
    .online-user-div{
        margin: 0.5rem 0; 
        display: flex;
        align-items: center;
        margin-bottom: 1rem;
    }

    .People-container {
        display: flex;
        align-items: center;

        .People-profile-img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
            object-position: center;
            margin: 0.5rem 0;
        }

        .People-profile-name {
            margin-left: 0.5rem;
            font-weight: 600;
            text-transform: capitalize;
        }
    }

}